const {test} = require('ava');
const promise = Promise.reject(new TypeError('ğŸ¦„'));

test('rejects', async t => {
	await t.throws(promise);
	//t.is(error.message, 'ğŸ¦„');
});

test('promise',t => {
	return Promise.resolve(1).then((res)=>{
	t.is(res,1);
	});
});

test('promise&async', async t => {
	t.plan(1);
	//t.falsy(await Promise.reject(1).catch((err)=>{console.log(err)}));
	t.truthy(await Promise.resolve(0).then((a)=>{console.log(a)}));
});

test.cb('cb',(t)=>{
t.plan(1);
t.is(1,1);
t.end();
});

test('test',(t)=>{
t.plan(1);
t.is(1,1);
});



